.enquireContainer {
  &:before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 500px;
    background-color: #83D8A8;
    z-index: -1;

    @media screen and (max-width: 490px) {
      top: 0;
    }
  }
  
  padding: 150px 15px 100px 15px;
  // position: absolute;
  // bottom: 0;

  @media screen and (min-width: 320px) {
    padding: 150px 15px 100px 15px;
  }

  text-align: center;
  // margin: 0 auto 200px auto;

  // max-width: 714px;
  color: white;

  background-image: url('../../../../assets/images/landing/footer_bg.svg');
  background-size: 600%;
  background-repeat: no-repeat;
  background-position: 40% -100%;
  
  @media screen and (min-width: 320px) {
    background-position: 40% -40%;
    background-size: 400%;
  }
  @media screen and (min-width: 500px) {
    background-position: 40% -20%;
    background-size: 400%;
  }
  @media screen and (min-width: 680px) {
    background-position: 40% 0%;
    background-size: 200%;
  }
  // @media screen and (max-width: 991px) {
  //   background-position: 40% 10%;
  // }

  h3 {
    color: white;
    font-weight: normal;
    margin-bottom: 30px;

    @media screen and (max-width: 991px) {
      font-size: 1.25rem;
    }
  }

  p {
    color: white;
    font-size: 1.25rem;

    margin-bottom: 30px;

    @media screen and (max-width: 991px) {
      font-size: 1rem;
    }
  }

  .messengerBtn {
    @extend %button-custom;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #387EDC;
    border: none;
    border-radius: 0.5rem;
    // border: 4px #FFFFFF88 solid;
    margin: 10px auto;
    width: 395px;
    max-width: 100%;

    font-weight: bold;
    text-shadow: 0 3px 5px rgba(black, 0.24);

    img {
      width: 52px;
      height: 52px;
      margin-right: 20px;
    }

    &:hover {
      background-color: #10a4d6;
    }

    p {
      margin: 0;
    }

    @media screen and (max-width: 991px) {
      img {
        width: 38px;
        height: 38px;
        margin-right: 20px;
      }  
    }
  }
}

.enquireInner {
  max-width: 714px;
  margin: auto;
}

.footerInfo {
  // position: absolute;
  // bottom: 0;
  background-color: #FAFBFF;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  min-height: 125px;
  padding-top: 20px;
  padding-bottom: 20px;

  @media screen and (max-width: 575px) {
    padding-bottom: 100px;
  }


  p {
    font-size: 1rem;
  }

  .companyInfo {
    @media screen and (max-width: 575px) {
      text-align: center;
      margin-top: 20px;
    }
  }

  .socials {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;

    @media screen and (max-width: 575px) {
      justify-content: center;
    }
  }

  .socialBtn {
    padding: 0 0 0 10px;
    @extend %button-custom;

    img {
      width: 45px;
      height: 45px;
    }
  }
}

.emailUs {
  margin-top: 30px;

  font-size: 1rem !important;
  a {
    color: #044C85 !important;
  }
}

.modal {
  [class*="modal-header"],
  [class*="modal-footer"] {
    border: none;
  }

  [class*="modal-content"] {
    border: none;
    padding: 20px;
  }

  button {
    border: none;
    border-radius: 0.5rem;
    span {
      font-weight: bold;
      text-shadow: 0 3px 5px rgba(black, 0.24);
    }
  }
}

.address {
  margin-bottom: 20px;
  font-size: 0.9rem;
  a {
    color: #8C8B99;
  }
}

.copyright {
  p {
    font-size: 0.9rem;
  }
}
